.lib-panel {
  margin-bottom: 20px; }

.lib-panel img {
  width: 100%;
  background-color: transparent; }

.lib-panel .row,
.lib-panel .col-md-6 {
  padding: 0;
  background-color: #FFFFFF; }

.lib-panel .lib-row {
  padding: 0 20px 0 20px; }

.lib-panel .lib-row.lib-header {
  background-color: #FFFFFF;
  font-size: 20px;
  padding: 10px 20px 0 20px; }

.lib-panel .lib-row.lib-header .lib-header-seperator {
  height: 2px;
  width: 26px;
  background-color: #d9d9d9;
  margin: 7px 0 7px 0; }

.lib-panel .lib-row.lib-desc {
  position: relative;
  height: 100%;
  display: block;
  font-size: 13px; }

.lib-panel .lib-row.lib-desc a {
  position: absolute;
  width: 100%;
  bottom: 10px;
  left: 20px; }

.row-margin-bottom {
  margin-bottom: 20px; }

.box-shadow {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1); }

.no-padding {
  padding: 0; }

.no-link {
  text-decoration: none; }

a {
  color: inherit; }

